import React from 'react';
import { FC } from 'react';
import { CarrierFeesTabsNames } from '../../pages/carrier-fees';
import { CarrierFeesByCompany } from '../../types';
import { Table, Text } from '../common';
import {
  CarrierFeesFootNotes,
  CarrierFeesTable,
  CarrierFeesTables,
  CarrierFeeTablesDescription,
  CompanyLogo,
  StyledCarrierFeesTabItem,
  StyledCarrierFeesTabs,
} from './styled';
import AtAndTLogo from '../../images/CarrierFees/Logos/AtAndT.png';
import TMobileLogo from '../../images/CarrierFees/Logos/TMobile.png';
import VerizonLogo from '../../images/CarrierFees/Logos/Verizon.png';
import TCRLogo from '../../images/CarrierFees/Logos/CampaignRegistry.png';
import USCellularLogo from '../../images/CarrierFees/Logos/USCellularLogo.png';
import shortid from 'shortid';
import CustomLink from '../common/Link';

type CarrierFeesByCompanyProps = {
  carrierFees: CarrierFeesByCompany;
};

const mapCompanyNameToLogoSrc: Record<string, string> = {
  'AT&T': AtAndTLogo,
  'T-Mobile': TMobileLogo,
  Verizon: VerizonLogo,
  'U.S. Cellular': USCellularLogo,
  TCR: TCRLogo,
};

export const renderCarrierFeesTable = (table: {
  link?: string;
  title: string;
  items: Record<string, string>[];
}) => {
  const headers = Object.keys(table.items[0]);

  return (
    <Table.Container>
      <Table.Wrapper>
        <Table>
          <Table.Header>
            <Table.Row>
              {headers.map((header) => (
                <Table.HeaderCell key={shortid.generate()}>{header}</Table.HeaderCell>
              ))}
            </Table.Row>
          </Table.Header>
          <Table.Body>
            {table.items.map((item) => (
              <Table.Row key={shortid.generate()}>
                {Object.values(item).map((value, index) => (
                  <Table.BodyCell key={shortid.generate()}>
                    {table.link && index === 0 ? (
                      <CustomLink address={table.link}>{value}</CustomLink>
                    ) : (
                      value
                    )}
                  </Table.BodyCell>
                ))}
              </Table.Row>
            ))}
          </Table.Body>
        </Table>
      </Table.Wrapper>
    </Table.Container>
  );
};

export const RenderCarrierFeesByCompany: FC<CarrierFeesByCompanyProps> = ({
  carrierFees,
}) => {
  return (
    <CarrierFeesTables>
      {mapCompanyNameToLogoSrc[carrierFees.companyName] && (
        <CompanyLogo>
          <img
            src={mapCompanyNameToLogoSrc[carrierFees.companyName]}
            alt={carrierFees.companyName}
          ></img>
        </CompanyLogo>
      )}
      {carrierFees.description && (
        <CarrierFeeTablesDescription>
          <Text.Regular.Normal.md>{carrierFees.description}</Text.Regular.Normal.md>
        </CarrierFeeTablesDescription>
      )}
      {carrierFees.tables.map((table) => {
        return (
          <CarrierFeesTable key={shortid.generate()}>
            <Text.Title.Bold.md>{table.title}</Text.Title.Bold.md>
            {renderCarrierFeesTable(table)}
          </CarrierFeesTable>
        );
      })}
      <CarrierFeesFootNotes>
        {carrierFees?.footNotes.map((footNote) => (
          <p key={shortid.generate()}>{footNote}</p>
        ))}
      </CarrierFeesFootNotes>
    </CarrierFeesTables>
  );
};

const CarrierFeesTabItem = (props: {
  title: string;
  isActive?: boolean;
  onClick: () => void;
}) => {
  return (
    <StyledCarrierFeesTabItem isActive={props.isActive} onClick={props.onClick}>
      {props.title}
    </StyledCarrierFeesTabItem>
  );
};

type CarrierFeesTabsProps = {
  activeTab: CarrierFeesTabsNames;
  setActiveTab: (activeTabe: CarrierFeesTabsNames) => void;
};

export const CarrierFeesTabs: FC<CarrierFeesTabsProps> = ({
  activeTab,
  setActiveTab,
}) => {
  return (
    <StyledCarrierFeesTabs>
      <CarrierFeesTabItem
        title={'AT&T'}
        isActive={activeTab === CarrierFeesTabsNames.AtAndT}
        onClick={() => setActiveTab(CarrierFeesTabsNames.AtAndT)}
      />
      <CarrierFeesTabItem
        title={'T-Mobile'}
        isActive={activeTab === CarrierFeesTabsNames.TMobile}
        onClick={() => setActiveTab(CarrierFeesTabsNames.TMobile)}
      />
      <CarrierFeesTabItem
        title={'Verizon'}
        isActive={activeTab === CarrierFeesTabsNames.Verizon}
        onClick={() => setActiveTab(CarrierFeesTabsNames.Verizon)}
      />
      <CarrierFeesTabItem
        title={'U.S. Cellular'}
        isActive={activeTab === CarrierFeesTabsNames.USCellular}
        onClick={() => setActiveTab(CarrierFeesTabsNames.USCellular)}
      />
      <CarrierFeesTabItem
        title={'TCR'}
        isActive={activeTab === CarrierFeesTabsNames.TCR}
        onClick={() => setActiveTab(CarrierFeesTabsNames.TCR)}
      />
      <CarrierFeesTabItem
        title={'Other'}
        isActive={activeTab === CarrierFeesTabsNames.Other}
        onClick={() => setActiveTab(CarrierFeesTabsNames.Other)}
      />
    </StyledCarrierFeesTabs>
  );
};
